.contact__container {
    justify-content : space-between;
}

.contact__container>div {
    flex    : 50%;
}

.contact__image {
    width: 100%;
}

.contact__container .image__container {
        margin-top      : 1rem;
        border          : 5px solid rgb(232 232 232 / 93%);
        width           : 28rem;
        height          : 31rem;
        overflow        : hidden;
        border-radius   : 25%
}

.contact__data {
    gap : .5rem;
}

.contact__all {
    margin-top      : 1rem;
    gap             : 2rem;
    border-radius   : 10px;
}

.contact__rows {
    gap         : 2rem;
}

.contact__item {
    background-color : black;
    color            : white;
    width            : 15rem;
    padding          : 1rem;
    border           : 5px solid rgba(137, 131, 131, 0.463);
    border-radius    : 20px;
    gap              : 1rem;
    transition       : all 300ms ease-in;
}

.contact__item:hover {
    scale: 1.1;
    cursor: pointer;

}

.contact__item>:nth-child(1) {
    width   : 100%;
    gap     : 1.5rem;
}

.contact__detail .blackText {
    font-size           : var(--h3-font-size);
    font-weight         : var(--font-semi-bold);
    color               : White;
}

.contact__icon {
    padding         : .5rem;
    background      : darkorange;
    border-radius   : 10px;
}

.contact__icon svg {
    fill            : black;
}

.contact__btn {
    padding             : .25rem;
    width               : 100%;
    background-color    : darkorange;
    color               : black;
    font-size           : var(--h3-font-size);
    font-weight         : var(--font-semi-bold);
    border-radius       : 5px;
}

.contact__btn:hover {
    color: white;
}

@media (max-width: 1024px) {
    .contact__container {
        flex-direction: column;
    }

    .contact__image {
        justify-content: center;
    }

    .contact__all {
        width: 100%;
    }

    .contact__rows {
        flex-direction  : column;
        width           : 100%;
        padding         : 1rem;
    }

    .contact__item{
        width: 100%;
    }
}

@media (max-width: 640px) {
    .contact__container .image__container {
        width       : 95%;
        height      : 25rem;
    }

    .contact__image {
        width       : 100%;
    }
}