.value__section {
    margin-top      : 3rem;
}
.value__container {
    justify-content: space-between;
}

.value__container>div {
    flex    : 50%;
}

.value__container .image__container {
    border          : 8px solid rgb(232 232 232 / 93%);
    width           : 30rem;
    height          : 35rem;
    overflow        : hidden;
    border-radius   : 15rem 15rem 0 0;
    margin-top      : 1rem;
}

.value__data {
    gap : .5rem;
}

.accordion {
    margin-top  : 2rem;
    border      : none;
}

.accordion__item {
    overflow        : hidden;
    margin-bottom   : 1rem;
}

.accordion__button {
    display             : flex;
    align-items         : center;
    cursor              : pointer;
    gap                 : 2rem;
}

.accordion__icon {
    padding         : .25rem;
    background      : black;
    border-radius   : 5px;
}

.accordion__icon svg {
    fill            : darkorange;
}

.accordion__button .blackText {
    font-size: var(--h1-font-size);
}

@media (max-width: 1024px) {
    .value__container {
        flex-direction: column;
    }
}

@media (max-width: 640px) {
    .value__container .image__container {
        width       : 95%;
        height      : 25rem;
    }

    .value__image {
        width       : 100%;
    }
}