/* Fonts Google */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

/* Variables CSS */
:root {
  --black: #131110;
  --secondary: rgba(255, 255, 255, 0.78);

  /* Font and Typography */
  --header-size       : 3rem;
  --big-font-size     : 2.5rem;
  --h1-font-size      : 1.5rem;
  --h2-font-size      : 1.2rem;
  --h3-font-size      : 1rem;
  --normal-font-size  : 0.938rem;
  --small-font-size   : 0.813rem;
  --smaller-font-size : 0.75rem;

  /* Font Weight */
  --font-normal    : 400;
  --font-medium    : 500;
  --font-semi-bold : 600;
}

/* BASE */
* {
  margin     : 0;
  padding    : 0;
  box-sizing : border-box;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
}

.paddings {
  padding: 1rem;
}

.innerWidth {
  width: 100%;
}

.flexCenter {
  display: flex;
  row-gap: 2rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.flexStart {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.flexEnd {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.flexColCenter {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.flexColStart {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.flexColEnd {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.orangeText {
  color: darkorange;
  font-size: var(--h1-font-size);
  font-weight: var(--font-semi-bold);
  margin-bottom: .5rem;
}

.blackText {
  color: black;
  font-weight: var(--font-semi-bold);
  font-size: 2rem;
}

.secondaryText {
  color: rgb(140 139 139);
  font-size: var(--h3-font-size);
}

a {
  text-decoration: none;
}

button {
  cursor      : pointer;
  border      : none;
  outline     : none;
}

/* Media Queries */
@media (min-width: 1536px) {
  .innerWidth {
   max-width: 1280px;
   margin: auto;
  }
}

@media (min-width: 640px) {
  .paddings {
    padding: 4rem;
  }
}

@media (min-width: 1280px), (min-width: 768px) {
  .paddings {
    padding: 2rem;
  }
}

@media (max-width: 640px) {
  .orangeText {
    font-size: 1.2rem;
  }
}






.App {
  position: relative;
  overflow: clip;
}

.App>:nth-child(1) {
  background: var(--black);
  position: relative;
}

.gradient {
  position: absolute;
  width: 20rem;
  height: 20rem;
  background: rgba(255, 255, 255, 0.522);
  filter: blur(110px);
  border-radius: 110px;
}