.dashboard__section {
    color               : white;
    padding-bottom      : 1rem;
    z-index: 1;
}

.dashboard__container {
    justify-content     : space-around;
    align-items         : flex-end;
    margin-bottom       : 1rem;
}

.dashboard__data {
    gap                 : 3rem;
}

.dashboard__title {
    position            : relative;
    z-index             : 1;
    font-size           : var(--big-font-size);
    font-weight         : var(--font-semi-bold);
    line-height         : 5rem;
}

.dashboard__icon {
    color           : darkorange;
}

.search {
    background-color    : white;
    justify-content     : space-between;
    border              : 3px solid rgba(120, 120, 120, 0.374);
    border-radius       : 5px;
    padding             : .25rem 1rem;
    width               : 100%;
}

.search>input {
    border      : none;
    outline     : none;
}

.dashboard_status {
    width           : 100%;
    justify-content : space-between;
}

.status__item>:nth-child(1) {
    font-size   : 2rem;
}

.status__item>:nth-child(1)>:last-child {
    color   : #ff922d;
}

.image__container {
    width           : 30rem;
    height          : 35rem;
    overflow        : hidden;
    border-radius   : 15rem 15rem 0 0;
    border          : 8px solid rgba(255,255,255, 0.12);
}

.image__container>img {
    width       : 100%;
    height      : 100%;
}

.dashboard__btn {
    font-weight         : var(--font-medium);
    background-color    : #ff922d;
    padding             : .5rem;
    border-radius       : 5px;
    transition          : all 300ms ease-in; 
}

@media (max-width: 640px) {
    .dashboard__section {
        padding-bottom      : 0rem;
    }

    .dashboard__container {
        margin-top: 1rem;
    }

    .dashboard__title {
        font-size   : var(--big-font-size);
        font-weight : var(--font-semi-bold);
        line-height : 4rem;
    }

    .image__container {
        width       : 95%;
        height      : 25rem;
    }

    .dashboard__image {
        width       : 100%;
    }

    .dashboard_status {
        justify-content: center;
        gap: 1.5rem;
    }

    .status__item>:nth-child(1) {
        font-size   : var(--h1-font-size)
    }
    
    .status__item>:nth-child(2) {
        font-size   : var(--normal-font-size)
    }
}


