.residencies__section {
    background-color    : white;
    margin-top      : 4rem;
}

.residencies__container {
    overflow            : hidden;
    position            : relative;
}

.residencies__head {
    gap : 5.rem;
    margin-bottom: 2rem;
}

.residencies__card {
    padding         : 1rem;
    gap             : .5rem;
    border-radius   : 10px;
    max-width       : max-content;
    margin          : auto;
    transition      : all 300ms ease-in;
}

.residencies__card:hover {
    scale: 1.025;
    cursor: pointer;
    background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(254, 153, 0, 0.925) 217.91%
    );
    box-shadow: 0px 72px 49px -51px rgba(136, 160, 255, 0.21);
}

.residencies__card>img {
    width       : 100%;
    max-width   : 15rem;
}

.residencies__card>:nth-child(2) {
    font-size       : var(--h2-font-size) ;
    font-weight     : var(--font-semi-bold);
}

.residencies__card>:nth-child(3) {
    color           : black;
    font-size       :var(--h1-font-size) ;
}

.residencies__card>:nth-child(4) {
    color           : rgb(140 139 139);
    font-size       :var(--smaller-font-size);
    width           : 15rem;
}

.tag {
    color   : orange;
}

.residencies__btn {
    margin-top      : 2rem;
    gap             : 1rem;
}

.residencies__btn button {
    font-size           : var(--h2-font-size);
    padding             : .25rem .75rem;
    color               : white;
    border-radius       : 5px;
    background-color    : black;
}

@media (max-width: 640px) {
    .residencies__head {
        align-items: center;
    }
}