.inner__container {
    background      : black;
    gap             : 1rem;
    padding         : 1rem;
    border          : 7px solid rgba(137, 131, 131, 0.463);
    border-radius   : 25px;
    text-align      : center;
}

.inner__container .orangeText {
    font-size     : 2rem;
    font-weight   : var(--font-semi-bold);
}

.start__item {
    color           : black;
}
  
.start__item:hover {
    color: white;
}

.start__btn {
    font-size           : var(--h2-font-size);
    font-weight         : var(--font-semi-bold);
    padding             : .25rem .75rem;
    border-radius       : 10px;
    background-color    : darkorange;
    transition          : all 300ms ease-in;
}

.start__btn:hover {
    scale: 1.1;
}