.header {
    color            : white;
    z-index: 99;
}

.header__container {
    justify-content : space-between;
    padding         : 0rem 2rem;
}

.header__menu {
    margin-right    : 2rem;
    gap             : 2rem;
    font-weight     : var(--font-medium);
}

.items__menu {
    color           : var(--secondary);
}

.items__menu:hover {
    color           : #ff922d;
}

.menu__icon {
    display         : none;
}

@media (max-width: 768px) {
    .menu__icon {
        display             : block;
    }

    .header__menu {
        position            : absolute;
        flex-direction      : column;
        align-items         : flex-start;
        background-color    : darkorange;
        box-shadow          : 0px 0px 4px rgba(185, 185, 185, 0.858);
        top                 : 3rem;
        right               : 4rem;
        padding             : 1rem;
        border-radius       : 10px;
        transition          : all 300ms ease-in;
        z-index: 99;
    }

    .items__menu {
        color               : white;
    }
    
    .items__menu:hover {
        color               : black;
    }
  }